.wrapper{

}
.TilesWrapper{
    display: grid;
    grid-template-columns: repeat(4,0.15fr);
    margin-left: 5%;
    margin-top: 5%;
    grid-column-gap: 5%;
    grid-row-gap: 5%;
    justify-content: center;

}

.Card{
    margin-top: 10px;
    width: 50px;
    height: 40px;
    border-radius: 10px;
}

@media (min-width: 576px) {
    .Card{
        width: 50px;
        height: 40px;
    }
}


@media (min-width: 768px) {
    .Card{
        width: 80px;
        height: 70px;
    }
}

@media (min-width: 992px) {

}

@media (min-width: 1200px) {
    .Card{
        width: 120px;
        height: 100px;

    }
}
